body {
  background-color: #BFBFBF;
  background: -webkit-gradient(linear, left top, left bottom, from(black), color-stop(black), to(#9F9F9F));
  background: -webkit-linear-gradient(black, black, #9F9F9F);
  background: -o-linear-gradient(black, black, #9F9F9F);
  background: linear-gradient(black, black, #9F9F9F);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh; }

.app-header {
  background-color: #000000;
  padding-bottom: 20px; }



