.control-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 auto; }

.control-buttons button {
  background-color: #FFE919;
  border: none;
  color: #000000;
  font-size: 18px;
  margin: 5px;
  padding: 10px 20px; }

.control-buttons button:hover {
  background-color: #FFF16F;
  cursor: pointer; }

.control-buttons button:focus {
  outline: 0; }

.people-button.active {
  background-color: #56C3E0; }

.people-button.active:hover {
  background-color: #80D2E8; }

.planets-button.active {
  background-color: #7EE056; }

.planets-button.active:hover {
  background-color: #9EE880; }

.vehicles-button.active {
  background-color: #EF4926; }

.vehicles-button.active:hover {
  background-color: #F16041; }

.icon {
  margin-right: 10px; }

.control-buttons .fav-button {
  background-color: #d861c9;
  padding-right: 3px; }

.control-buttons .fav-button:hover {
  background-color: #DD75D0; }

.fav-num {
  background-color: #6f2967;
  color: white;
  margin-left: 10px;
  padding: 7px 10px; }

@media screen and (max-width: 650px) {
  .control-buttons button {
    margin: 2px;
    padding: 10px; }
  .icon {
    display: none; } }



